"use client";

import React, { useState, useEffect } from "react";
import PostCreator from "./PostCreator";
import PostCard from "./PostCard";
import { useAuth } from "@/context/AuthContext"; // adjust path

interface PostFromBackend {
  id: string;
  user: { name: string; profilePic: string };
  feeling?: string;
  withUser?: string;
  createdAt: string;
  text: string;
  media?: { url: string; type: "image" | "video"; alt?: string }[];
  likedBy: string[];
  totalLikes: number;
  commentsCount: number;
  sharedCount: number;
}

const MiddleContent: React.FC = () => {
  const { user } = useAuth();
  const [posts, setPosts] = useState<PostFromBackend[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchPosts = async () => {
      try {
        setLoading(true);
        const res = await fetch("/api/posts", { method: "GET" });
        if (!res.ok) throw new Error("Failed to fetch posts");
        const data: PostFromBackend[] = await res.json();
        setPosts(data);
      } catch (err) {
        console.error(err);
      } finally {
        setLoading(false);
      }
    };

    fetchPosts();
  }, []);

  return (
    <main className="flex-1 h-screen p-4 space-y-4 overflow-y-auto scrollbar-custom mx-64 mr-64">
      <PostCreator />

      {loading
        ? Array.from({ length: 3 }).map((_, idx) => (
            <PostCard key={idx} postId="" /> // optional skeleton inside PostCard
          ))
        : posts.map((post) => (
            <PostCard key={post._id} postId={post._id} />
          ))}
    </main>
  );
};

export default MiddleContent;
