"use client";

import React, { useState } from "react";
import {
  Home,
  Hash,
  Heart,
  Clapperboard,
  Bell,
  User,
  Upload,
  MessageSquare,
  Bookmark,
  Activity,
  Moon,
  ChevronDown,
  ChevronUp,
} from "lucide-react";
import { useAuth } from "@/context/AuthContext";

const COLORS = {
  BACKGROUND_PRIMARY: "bg-[#131313]",
  BACKGROUND_SECONDARY: "bg-gray-800",
  BORDER_COLOR: "border-gray-800",
  TEXT_PRIMARY: "text-white",
  TEXT_SECONDARY: "text-gray-300",
  TEXT_HEADER: "text-gray-500",
  ACTIVE_ACCENT: "border-white",
  ACTIVE_BACKGROUND: "bg-gray-800",
};

const menuSections = [
  {
    title: "MAIN MENU",
    links: [
      { name: "Home Page", icon: Home, isActive: true },
      { name: "Trending Topics", icon: Hash },
      { name: "Popular Creator", icon: Heart },
      { name: "Reels", icon: Clapperboard },
      { name: "Notification", icon: Bell },
    ],
    defaultOpen: true,
  },
  {
    title: "MY MENU",
    links: [
      { name: "Profile", icon: User },
      { name: "Shot Upload", icon: Upload },
      { name: "Message", icon: MessageSquare },
    ],
    defaultOpen: true,
  },
];

const MenuItem = ({ icon: Icon, name, isActive = false, onClick = () => {} }) => (
  <div
    onClick={onClick}
    className={`flex items-center p-3 my-0.5 rounded-lg transition-colors cursor-pointer text-sm
      ${
        isActive
          ? `${COLORS.ACTIVE_BACKGROUND} ${COLORS.TEXT_PRIMARY} font-semibold border-r-4 ${COLORS.ACTIVE_ACCENT}`
          : `${COLORS.TEXT_SECONDARY} opacity-80 hover:${COLORS.BACKGROUND_SECONDARY}/50 hover:${COLORS.TEXT_PRIMARY}`
      }`}
  >
    <Icon className="w-5 h-5 mr-3" />
    <span>{name}</span>
  </div>
);

const MenuSection = ({ section }) => {
  const [isOpen, setIsOpen] = useState(section.defaultOpen);
  const headerClasses = `flex items-center justify-between text-xs font-bold tracking-widest ${COLORS.TEXT_HEADER} uppercase cursor-pointer mb-2`;
  const iconClasses = `w-3 h-3 ${COLORS.TEXT_HEADER}`;

  return (
    <div className="mb-6">
      <div className={headerClasses} onClick={() => setIsOpen(!isOpen)}>
        {section.title}
        {isOpen ? <ChevronUp className={iconClasses} /> : <ChevronDown className={iconClasses} />}
      </div>
      {isOpen && (
        <nav>
          {section.links.map((link, index) => (
            <MenuItem key={index} {...link} />
          ))}
        </nav>
      )}
    </div>
  );
};

const Sidebar = () => {
  const [isDarkMode, setIsDarkMode] = useState(true);
  const { user, logout } = useAuth();

  // ✅ Get fallback avatar using Picsum if not provided
  const avatarSrc =
    user?.avatarUrl ||
    `https://picsum.photos/200?random=${user?._id || Math.floor(Math.random() * 1000)}`;

  return (
    <aside
      className={`w-64 h-screen ${COLORS.BACKGROUND_PRIMARY} ${COLORS.TEXT_PRIMARY} p-4 flex flex-col shadow-2xl rounded-xl border ${COLORS.BORDER_COLOR}`}
    >
      {/* Logo */}
      <div className="flex items-center mb-8 pt-1">
        <h1 className={`text-3xl pl-1 font-serif italic ${COLORS.TEXT_PRIMARY}`}>DZINR-APP</h1>
      </div>

      {/* Menu Sections */}
      <div className="flex-grow">
        {menuSections.map((section, index) => (
          <MenuSection key={index} section={section} />
        ))}
      </div>

      {/* ✅ Login or Profile */}
      <div className="my-4 border-t border-gray-700 pt-4 flex justify-center">
        {user ? (
          <div className="flex items-center space-x-3 cursor-pointer hover:bg-gray-700 px-4 py-2 rounded-lg transition-all w-full justify-between">
            <div className="flex items-center space-x-3">
              <img
                src={avatarSrc}
                alt="Profile"
                className="w-10 h-10 rounded-full border border-gray-600"
              />
              <span className="font-medium text-white capitalize">{user.name}</span>
            </div>
            <button
              onClick={logout}
              className="text-xs bg-red-600 hover:bg-red-700 text-white py-1 px-3 rounded-md"
            >
              Logout
            </button>
          </div>
        ) : (
          <a
            href="/auth/login"
            className="w-full bg-gray-700 hover:bg-gray-600 text-white font-semibold py-2 px-8 rounded-lg shadow-md transition-all duration-200 flex items-center justify-center"
          >
            Login
          </a>
        )}
      </div>

      {/* Dark Mode Toggle */}
      <div
        className={`p-3 ${COLORS.BACKGROUND_SECONDARY} rounded-lg border ${COLORS.BORDER_COLOR} flex items-center justify-between shadow-inner`}
      >
        <div className={`flex items-center text-sm ${COLORS.TEXT_SECONDARY}`}>
          <Moon className={`w-5 h-5 mr-3 ${COLORS.TEXT_PRIMARY}`} />
          <span className={COLORS.TEXT_PRIMARY}>Dark Mode</span>
        </div>
        <label className="relative inline-flex items-center cursor-pointer">
          <input
            type="checkbox"
            checked={isDarkMode}
            onChange={() => setIsDarkMode(!isDarkMode)}
            className="sr-only peer"
          />
          <div
            className={`w-12 h-6 ${COLORS.BACKGROUND_SECONDARY} rounded-full peer-checked:bg-white transition-colors duration-300 shadow-md`}
          ></div>
          <div
            className={`absolute top-0.5 left-[3px] w-5 h-5 rounded-full transition-transform duration-300 shadow-lg ${
              isDarkMode ? "translate-x-[20px] bg-black" : "translate-x-0 bg-white"
            }`}
          ></div>
        </label>
      </div>
    </aside>
  );
};

export default Sidebar;
